import React,{Component} from 'react';
import './menuDisplay.css';
import {withRouter} from 'react-router-dom'
class MenuDisplay extends Component{
constructor(props){
    super(props);
    this.state={
        orders:[]
    }
}
orderId=[]
placerOrder=(id)=>{
this.orderId.push(`${id}`)
//console.log(this.orderId)
this.props.finalOrder(this.orderId)
}
/*proceed=()=>{
    sessionStorage.setItem('menu',this.orderId)
    sessionStorage.setItem('main',this.props.restId)
    this.props.history.push('/placeOrder')
}*/
 renderMenu=({menudata})=>{
        if(menudata){
            return menudata.map((item)=>{
                return(
                    <div key={item.menu_id}>
                   
                    <div className="col-md-4 margin">
                    <img src={item.menu_image} alt="earrings" className="imagemenu"></img>
                    </div>
                    <div className="col-md-4 margin menu">
                   {item.menu_name}-{item.menu_price}
                    </div>
                    <div className="col-md-4 margin1 ">
                    <button className="btn btn-danger btn1" onClick={()=>{this.placerOrder(item.menu_id)}}>+</button>
                    </div>
                </div>
            )
              
            })
        }
    }
    render(){
     console.log(">>>menu>>>",this.props)
             return(
                <div>
                    <div className="col-md-12 big-info">
                    {this.renderMenu(this.props)}
                    </div><br/>
                    <div className="row">
                        <div className="col-md-12">
                        <button className="btn btn-success" onClick={this.proceed}>Proceed</button>
                        </div>
                    </div>
                </div>
                
    )
     
    }
   
}
export default withRouter(MenuDisplay);